<template>
    <div>
        <el-container>
            <el-header class="homeHeader">
                <div class="title"></div>
                <div>
                    <el-button icon="el-icon-s-flag" type="text" style="margin-right: 8px;color: #ffffff;" @click="punchClock"></el-button>
                    <el-button icon="el-icon-message-solid" type="text" style="margin-right: 8px;color: #ffffff;" size="normal" @click="goChat"></el-button>
                    <el-dropdown class="userInfo" @command="commandHandler">
                      <span class="el-dropdown-link" style="color: #ffffff">
                        {{user.name}}<i><img :src="user.userface" alt=""></i>
                      </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userinfo"></el-dropdown-item>
                            <el-dropdown-item command="setting"></el-dropdown-item>
                            <el-dropdown-item command="logout" divided></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px" style="font-family: 0;color: #1d5b74">
                    <el-menu router unique-opened>
                        <el-submenu :index="index+''" v-for="(item,index) in routes" v-if="!item.hidden" :key="index">
                            <template slot="title">
                                <i style="color: #1d5b74;margin-right: 5px" :class="item.iconCls"></i>
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item :index="child.path" v-for="(child,indexj) in item.children" :key="indexj">
                                {{child.name}}
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$router.currentRoute.path!='/home'">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <div class="homeWelcome" v-if="this.$router.currentRoute.path=='/home'">
                        <div style=" margin-top: 120px;font-size: 70px;text-align: center;color: #f4f4f4">{{dateFormat(new Date())}}</div>
                        <div style="font-size: 70px;text-align: center;color: #f4f4f4" v-text="date"></div>
                        <div style="font-size: 50px;text-align: center;color: #f4f4f4;font-family: 'JetBrains Mono Medium'">welcome come to vhr</div>
                    </div>
                    <router-view class="homeRouterView"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data() {
            return {
                //user: JSON.parse(window.sessionStorage.getItem("user")),
                attendance:{
                    clickTime:"",
                    username:"",
                    clickDate:"",
                },
                date: null

            }
        },
        computed: {
            routes() {
                return this.$store.state.routes;
            },

            user(){
                return this.$store.state.currentHr;
            }
        },

        mounted() {
            let _this = this; // Vuethis
            this.timer = setInterval(() => {
                _this.date = this.dateFormatTime(new Date()); // date
            }, 1000)
        },

        beforeDestroy() {
            if (this.timer) {
                clearInterval(this.timer); // Vue
            }
        },

        methods: {

            dateFormatWithTime(time) {
                const date = new Date(time);
                const year = date.getFullYear();
                /* 00
                 * 100   09:11:05
                 * */
                const month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
                const day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
                const hours = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
                const minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
                const seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
                // 
                return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
            },

            dateFormatTime(time) {
                const date = new Date(time);
                /* 00
                 * 100   09:11:05
                 * */
                const hours = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
                const minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
                const seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
                // 
                return hours+":"+minutes+":"+seconds;
            },

            dateFormat(time) {
                const date = new Date(time);
                const year = date.getFullYear();
                /* 00
                 * 100   09:11:05
                 * */
                const month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
                const day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
                // 
                return year+"-"+month+"-"+day;
            },

            punchClock(){
                this.attendance.clickTime = this.dateFormatWithTime(new Date());
                this.attendance.clickDate = this.dateFormat(new Date());
                this.attendance.username = this.user.name;
                this.postRequest("/employee/attendance/", this.attendance).then(resp => {
                    if (resp) {
                    }
                })
            },

            goChat() {
                this.$router.push("/chat");
            },
            commandHandler(cmd) {
                if (cmd == 'logout') {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest("/logout");
                        window.sessionStorage.removeItem("user")
                        this.$store.commit('initRoutes', []);
                        this.$router.replace("/");
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                }else if (cmd == 'userinfo'){
                    this.$router.push('/hrinfo');
                }
            }
        }
    }
</script>

<style>

    [v-cloak]{
        display: none;
    }

    .homeRouterView {
        margin-top: 10px;
    }

    .homeWelcome {
        /*text-align: center;
        font-size: 30px;
        font-family: "PingFang SC";
        color: #409eff;
        padding-top: 50px;*/
        background-size: 100% 100%;
        background: url('../images/2.jpg') center center no-repeat;
        height: 85%;
        position: fixed;
        width: 85%
    }

    .home-container {
        margin-top: 10px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .homeHeader {
        background-color: #1d5b74;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 15px;
        box-sizing: border-box;
    }

    .homeHeader .title {
        font-size: 30px;
        font-family: "PingFangSC-Regular";
        color: #ffffff
    }

    .homeHeader .userInfo {
        cursor: pointer;
    }

    .el-dropdown-link img {
        width: 48px;
        height: 48px;
        border-radius: 24px;
        margin-left: 8px;
    }

    .el-dropdown-link {
        display: flex;
        align-items: center;
    }


</style>